import { onWindowResize } from 'utils/index';
import { GLOBAL_VARS } from 'utils/constants';
import Swiper, { Navigation, Pagination } from 'swiper';
import buildSliders from './buildSliders';

const OurProductsSlider = () => {
	const sliderClass = '.ourProductsSlider';
	const $currentSlideIndex = document.querySelector('.ourProductsCurrent');
	const $totalSlideIndex = document.querySelector('.ourProductsTotal');
	const currentSlider = document.querySelector(sliderClass);
	const sliderArrows = document.querySelector('.our_products__navigation');
	const sliderProgress = document.querySelector('.our_products_bottom');
	let isMobile;
	let isTablet;
	const checkIsMobile = () => {
		isMobile = window.innerWidth < GLOBAL_VARS.mediaPoint2;
		isTablet = window.innerWidth < GLOBAL_VARS.mediaPoint1;
	};

	onWindowResize(checkIsMobile);

	buildSliders(sliderClass);

	let slideEl = document.querySelectorAll(sliderClass);
	let sliderLength = document.querySelectorAll('.ourProductsSlider .swiper-slide:not(.swiper-slide-duplicate)').length;
	if (typeof (slideEl) !== 'undefined' && slideEl != null) {
		let OurProductsSliderEl = new Swiper(sliderClass, {
			modules: [Navigation, Pagination],
			observer: true,
			observeParents: true,
			speed: 800,

			navigation: {
				nextEl: '.our_products__navigation .our_products__arrow--next',
				prevEl: '.our_products__navigation .our_products__arrow--prev',
			},

			pagination: {
				el: '.ourProductsProgress',
				type: 'progressbar',
				clickable: true,
			},

			breakpoints: {
				320: {
					slidesPerView: 1.126,
				},
				768: {
					slidesPerView: 1.27,
				},

				1024: {
					slidesPerView: 2,
					loop: false,
				},

				1280: {
					slidesPerView: 3,
					loop: true,
				},
			},

			on: {
				init: (slider) => {
					const index = currentSlider.querySelectorAll('.swiper-slide:not(.swiper-slide-duplicate)').length;
					$totalSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
				},
				slideChange: (slider) => {
					const index = slider.realIndex + 1;
					$currentSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
				},
			},
		});

		if ((!isTablet && sliderLength < 4) || (isTablet && sliderLength < 2)) {
			OurProductsSliderEl.destroy();
			currentSlider.classList.add('is-destroy');
			sliderArrows.classList.add('hidden');
			sliderProgress.classList.add('hidden');
		} else {
			OurProductsSliderEl.init();
			currentSlider.classList.remove('is-destroy');
			sliderArrows.classList.remove('hidden');
			sliderProgress.classList.remove('hidden');
		}
	}
};

export default OurProductsSlider;
